.vertical-timeline-element-content {
  position: relative;
  margin-left: 15%;
  text-align: left;
  background: transparent;
  box-shadow: none;
  padding-left: 0px;
}

.vertical-timeline:before {
  content: '';
  position: absolute;
  top: 0;
  left: 18px;
  height: 100%;
  width: 4px;
  border: solid 2px #ffa719;
}

.vertical-timeline-element-icon {
  position: absolute;
  top: 0;
  left: 0;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  box-shadow: -4 0 0 0px transparent, inset 0 0px 0 transparent,
    0 3px 0 4px transparent;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  background-image: url(/cypa-yeps-website/static/media/discussion.37c86acf.png);
  background-repeat: no-repeat;
  background-size: cover;
}

.App-intro-title {
  background-image: url(/cypa-yeps-website/static/media/wave.31ff7cd8.png);
  background-repeat: no-repeat;
  background-size: 80%;
}

.wave {
  width: 100%;
  height: 100%;
  object-fit: contain;
  background-image: url(/cypa-yeps-website/static/media/wave.c083a55f.svg);
  background-repeat: no-repeat;
}

.keynotes {
  background-color: #f7f7f7;
}

.speakers {
  opacity: 0.93;
  background-image: -webkit-linear-gradient(top, #1ea4c5, #0d6b93);
  background-image: linear-gradient(to bottom, #1ea4c5, #0d6b93);
}

.agenda {
  background-color: #f7f7f7;
}

.connections {
  background-color: #ffffff;
}

.join-us {
  background-image: -webkit-linear-gradient(top, #1ea4c5, #0d6b93);
  background-image: linear-gradient(to bottom, #1ea4c5, #0d6b93);
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

